
@import url('https://fonts.googleapis.com/css2?family=Smooch&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Crete+Round&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pridi:wght@200;300;400;500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300&display=swap');


* {
    
    padding: 0px;
    margin: 0px;
    font-family: sans-serif;
    margin: 0 ;
    
}

body{
    background-color: #e7e7e7;
    background-color: rgb(240, 242, 245);
    color: #ffff;
    margin: 0;
    padding: 0;
}

.app {
    overflow: hidden;
}

.dashboard {
    max-width: 100%;
    min-height: 100vh !important;
    height:100%;
    padding: 0 !important;
    margin: 0 !important;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    
    .containerHeader {
        height: 430px;
        width: 100vw;
        overflow: hidden;
        position:absolute;
        top: 0;
        z-index: -10;
        text-align: center;
        padding: 0 !important;
        overflow: hidden;
        width: 100%;
        max-width: 100vw;
    }

    .containerHeader .headerImg {
        height: 560px;
        width: 100%;
        max-width: 100vw ;
        margin: 0;
    }
}

.dashboard .main {
    width: 93%;
    max-width: 1400px;
    height: auto;
    padding: 10px;
    background-color: #ffff;
    border-radius: 10px;
    box-shadow: rgba(131, 131, 131, 0.651) -1px 15px 25px;
    margin: 50vh 0px 10px 0;

    .presentacion {
        color: #222; 
        display: flex;
        flex-direction: column;
        justify-content: center;
        justify-items: center;
        align-items: center;
        gap: 20px;
        padding: 20px;
        margin-top: -137px;

        .header {
            display: flex;
            flex-direction: column;
            justify-items: center;
            align-items: center;
            gap: 10px;
            margin: 1px 0;

            .titulo {
                font-family: 'Pridi', serif;
                font-weight: 600;
                letter-spacing: 1px;
                color: #000000d7;
            }

            .sub-titulo {
                font-family: 'Open Sans', sans-serif;
                font-weight: 500;
            }

        }

        .container-msg-principal {
            font-family: 'Montserrat', sans-serif;
            font-family: 'Hammersmith One', sans-serif;
            color: rgb(88, 88, 88);
            max-width: 930px;
            letter-spacing: 0.1px;
            text-align: center;
            padding: 20px 0px;
            font-weight: 300;
            line-height: 1.5em;
        }

        .container-logo {
            background-image: -webkit-radial-gradient(center, rgb(240, 142, 22), rgb(238, 133, 4));
            width: 200px;
            height: 200px;
            text-align: center;
            overflow: hidden;
            border-radius: 50%;
            position: relative;
            z-index: 100;
            box-shadow: rgba(174, 175, 175, 0.973) -0.5px 5px 5px;
        }
        
        
        .container-logo .foto-logo {
            height: 270px;
            float: center;
            margin-top: -30px;
        }
        
        .container-logo::before {
            content: '';
            width: 200px;
            height: 200px;
            background-color: rgb(25, 25, 43);   
            position: absolute;
            z-index: -100;
            left: 0;
            bottom: -132.3px;
            border-radius: 40%;
            left: 3.9px;
        }
    }

    .container-navigation {
        height: 170px;
        color: #000;

        display: flex;
        justify-content: center;
        align-items: center;

        .nav-item {
            height: 110px;
            width: 105px;
            margin: 10px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            justify-content: space-evenly;
            border-radius: 5px;
            color:#575757;
            
                      

            .icon{
                font-size: 30px;
            }

            span{
                font-size: 13px;
                font-family: 'Open Sans', sans-serif;
                font-weight: 700;
            }
        }

        .nav-item:hover {
            background-color: #f0efef;
            span {
                font-weight: 800;
            }
        }
    }

    
}

.section-selected {
    background-color:#0cdefa !important;
    color: #fafafa !important;
}

.dashboard .footer {
    color: #000;
    width: 90%;
    height: 80px !important;
    padding: 40px 0 0px 0px;

    .creditos {
        display: flex;
        gap: 10px;
        justify-content: center;
        align-content: center;
        align-items: center;

        .container-email {
            display: flex;
            gap: 5px;
            justify-content: center;
            .year, .email {
                text-align: center;
                display: flex;
                justify-items: center;
                align-items: center;
                padding: 10px 5px;
                font-weight: 200;
                color: rgba(97, 97, 97, 0.945);
            }
        }

        .redes-sociales {
            margin-left: 15px;
            display: flex;
            justify-items: center;
            align-items: center;
            gap: 15px;

            .item {
                height: 100%;
                display: flex;
                justify-items: center;
                align-items: center;

                .item-icon {
                    font-size: 35px;
                    color: rgba(97, 97, 97, 0.945); 
                   
                }
                .item-icon:hover{
                    color:crimson;
                }
            }
            .linkedin .item-icon{
                font-size: 41px;
            }

            .instagram .item-icon{
                font-size: 37px;
            }
            
        }
    }
}

@media (max-width: 490px) {
    .containerHeader .headerImg {
        height: 410px !important;
    }
    .main {
        margin-top: 350px !important;
    }
    .dashboard .footer {
        height: 105px !important;
    }
    .creditos  {
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        justify-items: center;
        align-items: center;
        margin-bottom: 100px !important;
        
    }
}


