@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;0,400;0,500;0,700;0,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Hammersmith+One&display=swap');
.dashboard .main .container-pages {
    padding-bottom: 100px;
    margin-top: 30px;
    * {
        font-family: 'Open Sans', 'Montserrat', sans-serif;
        color: #000;
    }
    

    .about-section .about-item {
        max-width: 1200px;
        width: 90vw;
        margin: auto;
        border: 1px solid #f6f6f6;
        
        border-radius: 5px;
        padding: 10px;
        margin-bottom: 15px;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
        gap: 15px;
        grid-template-areas: "A"
                             "B";

       .container-titulo-seccion  {
           grid-area: 'B';
           text-align: center;
           align-items: center;
           color: rgb(235, 231, 231);

           
           h1 {          
            font-size: 18px;
            font-weight: 500;
            color: rgba(102, 102, 102, 0.295);
            font-family: 'Hammersmith One', sans-serif;
           }
            
       }


       .item {
            padding: 0px !important;
            margin: 10px 0px;
            display: grid;
            grid-template-columns: 49% 45%;        
            gap: 10px;


           .container-titulo {
                height: 100%;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: flex-start;
                padding-left: 5%;
                
               .titulo {
                    color:#000;
                    font-size: 15px;
                    font-weight: 500;
               }
               
           }

           .descripcion {
                height: 100%;
                width: 100%;
                margin: auto;
                display: flex;
                text-align: center;
                flex-direction: column;
                align-items: center;
                justify-content: center;

                * {
                    font-size: 14px;
                    color: rgb(107, 107, 107);
                }
            }        
       }
    }

    .container-herramientas {
        max-width: 1200px;
        margin: auto;
        margin-top: 50px;
        padding: 10px;
        .container-titulo {
            margin: 20px 20px 30px 20px;
            .titulo {
                font-weight: 800;
                font-family: 'Open Sans', 'Montserrat', sans-serif;
                font-family: 'Hammersmith One', sans-serif;
                font-size: 18px;
                font-weight: 200;
                color: rgba(102, 102, 102, 0.295);
            }
        }

        .listado {
            display: flex;
            width: 100%;
            margin: auto;
            flex-direction: row;
            justify-content: center;
            flex-wrap: wrap;
            gap: 10px;

            .skill {
                height: 140px;
                width: 240px;
                background-color: rgba(235, 235, 235, 0.856);
                border-radius: 7px;
                padding: 10px;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;

                .skill-img {
                    height: 60%;
                    z-index: 400;
                    transition: ease-in-out 0.3s;
                }

                .skill-nombre {
                    position: absolute;
                    bottom: 10px;
                    color: #f0ecec;
                    font-size: 12px;
                    font-weight: 600;
                }
            }

            .skill:hover {
                cursor: pointer;
                .skill-img {
                    height: 90%;
                }
            }
            
            .HTML {
                background-image: -webkit-radial-gradient(center, rgb(255, 238, 0) 5%,rgba(255, 123, 0, 0.87) 100%); 
            }
            .CSS {
                background-image: -webkit-radial-gradient(center, rgb(14, 99, 121) 5%,rgb(6, 192, 224) 100%); 
            }
            .SASS {
                background-image: -webkit-radial-gradient(center, rgb(20, 122, 148) 10%,rgb(7, 227, 243) 100%); 
            }
            .BOOTSTRAP {
                background-image: -webkit-radial-gradient(center, rgb(128, 21, 119) 5%,rgb(211, 12, 211) 100%); 
                .skill-img {
                    height: 87%;
                    max-width: none;
                }
            }

            .BOOTSTRAP:hover .skill-img {
                height: 120%;
            }

            .JAVASCRIPT {
                background-image: -webkit-radial-gradient(center, rgb(0, 130, 190) 10%, rgb(3, 148, 184) 100%); 
                background-color: steelblueS;
            }
            .REACT {
                background-color: rgb(51, 59, 70);
            }
            .REDUX {
                background-image: -webkit-radial-gradient(center, rgb(177, 4, 85) 15%,rgb(252, 35, 180) 100%); 
            }
            .NODE {
                background-image: -webkit-radial-gradient(center, rgb(8, 253, 8) 20%, rgb(7, 175, 7) 100%); 
            }
            .NEXT {
                background-image: -webkit-radial-gradient(center, #c7c6c6b2 20%, #dddddda9 100%); 
            }
            .JAVA {
                background-image: -webkit-radial-gradient(center, rgb(28, 60, 75) 20%, rgb(36, 50, 59) 100%); 
            }
            .MONGO {
                background-image: -webkit-radial-gradient(center, rgb(41, 192, 3) 40%, rgb(6, 156, 6) 100%); 
                .skill-img {
                   height: 27%;
                   max-height: 35% !important;
                } 
            }
            .FIREBASE {
                background-color: rgb(4, 194, 241);
                .skill-img {
                    height: 27%;
                    max-height: 35% !important;
                 } 
            }
            .POSTGRES {
                background-color: rgb(68, 68, 68);
                background-image: -webkit-radial-gradient(center, rgb(43, 43, 43) 15%, rgb(68, 68, 68) 100%);  
            }
            .MySQL {
                background-image: -webkit-radial-gradient(center, rgb(15, 57, 80) 15%, rgb(25, 70, 94) 100%);  
            }
            .HEROKU {
                background-color: darkviolet;
            }
            .POWER {
                background-color: rgb(240, 204, 3);
            }
            .GIT {
                background-image: -webkit-radial-gradient(center, rgb(28, 60, 75) 20%, rgb(36, 50, 59) 100%); 
            }
            .METODOLOGIAS {

                background-image: -webkit-radial-gradient(center, #c7c6c6b2 20%, #dddddda9 100%); 
                .skill-img {
                    height: 70%;
                }
            }
        }

        


    }
}



@media (max-width: 490px) {
    .listado {
        display: grid !important;
        grid-template-columns: 45% 45%;
        grid-template-rows: auto;
        padding: 10px 0;

        .skill {
            width: 90% !important;
            height: 105px !important;
            .skill-img {
                height: 55% !important;
            }
            .skill-nombre {
                font-size: 10px !important;
            }
        }

        .MONGO, .FIREBASE {
            .skill-img {
                height: 40% !important;
            }
        }
        .BOOTSTRAP .skill-img {
            height: 85% !important;
        }
    }

    .about-item {
        width: 100vw !important;
        padding: 10px !important;
        width: 89vw !important;

        .item {
            height: auto !important;
            display: flex !important;
            flex-direction: column !important;
            justify-content: center;
            justify-items: center;
            gap: 10px;
            padding: 10px !important;
            
            .container-titulo, .descripcion {
                display: flex !important;
                justify-content: center !important;
                justify-items: center !important;
                padding: 0 !important;

            }

        }
    }
}


