.projects-section {
    padding: 5%;

    .project {
        margin-bottom: 10px;
        height: 400px;
       
        display: grid;
        grid-template-columns: 50% 50%;
        gap: 15px;
        overflow: hidden;
        padding: 20px;

        .container-img {
            position: relative;
            border-radius: 6px;
            width: 100%;
            
            display: flex;
            justify-content: center;
            align-content: center;

            .project-img {
                max-width: 100%;
                border-radius: 5px;
                transition: ease 0.2s !important;
                
                
            }

            .project-img:hover {
                cursor: pointer;
                box-shadow: rgba(83, 83, 83, 0.486) 0px 2px 5px 2px;
            }
            
        }

        .container-section-descripcion, .container-section-descripcion-img {
            cursor: pointer;
            border: 1px solid rgba(204, 204, 204, 0.74);
            background-color: #fff;
            border-radius: 5px;
            padding: 20px;
            max-width: 560px;
            display: grid;
            grid-template-rows: 1fr 60% 1fr;
            transition: all 0.3s;

            .container-titulo {
                font-family: 'Montserrat', sans-serif;
                display: flex;
                justify-content: center;
                align-items: center;
               
                .titulo {
                    font-weight: 600;
                    color: #222;
                }
            }

            .container-descripcion{
                display: flex;
                justify-content: center;
                align-items: center;
                align-content: center;
                text-align: center;
            }

            .container-btn {
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: center;
                align-items: center;

                .btn-ir {
                    width: 80px;
                    height: 45px;
                    border: 1px solid rgba(204, 204, 204, 0.685);
                    border-radius: 5px;
                    text-align: center;
                    text-decoration: none;
                    font-weight: 600;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 17px;
                   
                    transition: all 0.2s;
                    

                    .icon {
                        font-size: 30px;
                    }
                }
            }

        }

        .container-section-descripcion-img {
            position: absolute;
            height: 256px;
            max-width: none;
            width: 95%;
            padding: 0px 10px 12px 12px;
            
            background-color: #f8f8f8a6;
            background-color:#0cdefada !important;
            top:-500px;
            transition: ease 0.2s;
        }

        .container-section-descripcion:hover, .container-section-descripcion-img:hover {
            background-color:#0cdefa;
            .btn-ir:hover {
                background-color: rgba(236, 233, 233, 0.623);
            }
        }

    }
}


@media (max-width: 900px) {
    .project {
        height: 270px !important;
        grid-template-columns: 1fr !important;

        .container-section-descripcion {
            display: none !important;
        }

        .project-img {
            cursor: pointer;
        }
        .container-img:hover {

            .container-section-descripcion-img {
                top:0 !important;
            }
        }
    }
    
}







